.approveModalContainer {
    position: absolute;
    top: 50%;
    left: 50%;
    right: auto;
    bottom: auto;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    width: 100%;
    max-width: 1000px;
    min-height: 590px;
    outline: none;
    margin: 0 16px;
}

.overlayContainer {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
}

.summerModalContainer {
    width: 100%;
    height: auto;
    overflow-y: none;
    background: transparent linear-gradient(307deg, #090909 0%, #5a5a5a 100%) 0% 0% no-repeat padding-box;
    border-radius: 10px;
    padding: 16px 24px;
}