.approveModalContainer {
  position: absolute;
  top: 50%;
  left: 50%;
  right: auto;
  bottom: auto;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  width: 100%;
  max-width: 700px;
  height: auto;
  background: #ffffff;
  border-radius: 10px;
  outline: none;
  margin: 0 16px;
  border: none;
  outline: none;
}

.overlayContainer {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
}

.composeModalHeader {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  margin-top: -1px;
}

